<template>
  <div class="view aside">
    <main class="main">
      <div class="tool-bar fixed">
        <el-button @click="APP.goTo('home',true)">放弃编辑</el-button>
        <el-button @click="save" type="primary" class="button">保存</el-button>
      </div>
      <div class="tool-bar"></div>

      <el-collapse v-model="activeNames">
        <!-- title 用于显示在页面上，name用于展开的collapse与activeNames配合 -->
        <el-collapse-item :title="'页面数据项目维护'" :name="'0'">
          <c-table
            class="box-card"
            v-model="model"
            :dsl="APP.dslArray"
            :rules="APP.rules"
            :tableInfo="APP.dslArray"
            ref="topTabe"
          />
        </el-collapse-item>
      </el-collapse>
    </main>
  </div>
</template>

<script>
import Main from "./Main.js";
import $ from "jquery";

export default {
  name: "HqbjHjHyjc",
  mixins: [Main],
  inject: ["APP"],
  mounted() {
    /* get dslArray model from remote server
        and then set this.currentModelAdd this.dslArray
        */

    var vm = this;
    /*     $.ajax({
      url: "/scha-web/a/fnbj/hys/asideList",
      success: function(result) {
        vm.asideTree = result.data;
      }
    }); */
    // this.asideTree = asideTree;

    /* 模拟获取数据 */
    /*  this.$post("http://127.0.0.1:3000/n/test", {
          name: "asdfasdfsf",
          pwd: "asdfsf"
        }).then(response => {
          console.log(response);
        }); */

    /*     setTimeout(() => {
      this.asideTree = asideTree;
      this.setCurrentModelAdd(model);
      this.setcurrentImgListModel(imgListModel);
    }, 1);
 */
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      console.warn("beforeRouteEnter", to.name);
      let command = to.name;
      const initmodel = {
        add() {
          /* 置空 */
          vm.model = (model => {
            let obj = {};
            for (let key in model) {
              obj[key] = "";
            }
            return obj;
          })(vm.APP.model);
          console.log(vm.model);
        },
        update() {
          /* 初始化新增model */
          vm.model = JSON.parse(JSON.stringify(vm.APP.model));
        }
      };
      initmodel[command] && initmodel[command]();
    });
    // 在渲染该组件的对应路由被 confirm 前调用
    // 不！能！获取组件实例 `this`
    // 因为当守卫执行前，组件实例还没被创建
  },

  data() {
    return {
      model: {},
      /* 动态数据 */
      activeNames: ["0"]
    };
  },
  methods: {
    async save() {
      console.log(this.APP.model);

      const loading = this.$loading();
      try {
        /* 验证表单是否都符合rule */
        if (await this.$refs.topTabe.valid()) {
          this.APP.modelAdd = this.model;
          this.APP.save();
        } else {
          throw new Error("请按照提示检查输入项");
        }
      } catch (error) {
        this.$message({
          showClose: true,
          message: error.message,
          type: "warning"
        });
        loading.close();
      }
    }
  },
  computed: {
    treeOptions() {
      let treeOptions = {
        props: {
          children: "children",
          label: "mc"
        }
      };
      treeOptions.data = this.asideTree;
      treeOptions.showCheckbox = true;
      return treeOptions;
    }
  }
};
</script>

