<template>
  <div class="view aside">
    <aside class="pickup">
      <c-pickup-tree :treeOptions="APP.treeOptions" @node-click="APP.handleClickAside"></c-pickup-tree>
    </aside>
    <main class="main">
      <div class="tool-bar fixed">
        <el-button @click="APP.goTo('add')">新增</el-button>
        <el-button @click="APP.remove" :disabled="!Boolean(APP.model.jcdh)">删除</el-button>
      </div>
      <div class="tool-bar"></div>
      <el-collapse v-model="activeNames">
        <!-- title 用于显示在页面上，name用于展开的collapse与activeNames配合 -->
        <el-collapse-item :title="'页面数据项目维护'" :name="'0'">
          <c-table
            class="box-card"
            v-model="APP.model"
            :dsl="APP.dslArray"
            :rules="APP.rules"
            :tableInfo="APP.dslArray"
            ref="topTabe"
          />
        </el-collapse-item>
      </el-collapse>
    </main>
  </div>
</template>

<script>
import Main from "./Main.js";
import $ from "jquery";

export default {
  name: "FnbpcFkjc",
  mixins: [Main],
  inject: ["APP"],
  mounted() {
    this.APP.getAsideList();
  },
  data() {
    return {
      /* 动态数据 */
      activeNames: ["0"]
    };
  },
  methods: {
    /* 完成刷新前的数据检测 */
    async save() {
      try {
        /* 验证表单是否都符合rule */
        if (await this.$refs.topTabe.valid()) {
          this.APP.save();
        } else {
          throw new Error("请按照提示检查输入项");
        }
      } catch (error) {
        this.$message({
          showClose: true,
          message: error.message,
          type: "warning"
        });
      }
    },
    getTitle(fix) {
      return this.APP.model.xm
        ? `${fix}-${this.APP.model.xm}-${this.APP.model.bh}`
        : fix;
    }
  }
};
</script>

